.Mypic{
    border-radius:200px;
    margin-top:3em;
    margin-bottom:3em;
    width:200px;
    box-shadow: -30px 30px 20px rgba(0,0,0,0.4);
    /* -webkit-box-shadow: 0 0 30px rgb(0, 0, 0);
    box-shadow: 0 0 30px rgb(0, 0, 0); */
    transition: 0.4s;
    cursor:pointer;
}

.Mypic:hover{
    width:250px;
    border-radius: 20px;
}

.bottompic{
    -webkit-box-shadow: 0 0 30px rgb(0, 0, 0);
    box-shadow: 0 0 30px rgb(0, 0, 0);
    margin-bottom: 1em;
    border-radius: 200px;
    transition: 0.4s;
}

.bottompic:hover{
    width:120px;
   
}

