   background-color:#30258d;

}

.secdiv{
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}

.imgdiv{
    display:flex;
    flex-direction:column;
    justify-content: space-evenly;
    align-items: center;
    
}

.createdby{
    text-shadow: 2px 2px 2px #000000;
    font-size:12px;
}

.myname{
    color: rgb(80, 207, 165);
    text-shadow: 2px 2px 2px #000000;
    margin:1em;
    
    transition: 0.4s;
    font-weight:900;
    font-family: 'Julius Sans One', sans-serif;
    cursor: pointer;
    
}


.myname:hover{
    color:rgb(0, 163, 109) ;
    text-shadow: 2px 2px 2px #000000;
}

.title{
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items: center;
    padding-left:2.5em;
    padding-right:2.5em;
    
    
    
}





.p{
    color:rgb(230, 230, 230);
    font-size:15px;
    font-family: 'Mali', cursive;
    font-style: italic;
    padding:1em;
   
}


.giffy{
    box-shadow: -30px 30px 20px  rgba(0,0,0,0.4);
    transition: 0.4s;
    cursor: pointer;   
    width:160px; 
}

.giffy:hover{
    box-shadow:none;
}

.mygif{
    width:200px;
    transition:0.4s;
    cursor: pointer;
    
}

.mygif:hover{
    width:220px;
}



.bottomdiv{
    background-color: rgb(109, 0, 211);
    box-shadow: -30px -30px 20px  rgba(0,0,0,0.4);
    
}


.iconz{
    display:flex;
    margin-right:10px;
    margin-left: 10px;
    flex-direction: row;
    align-items: center;
    margin-top: 1.4em;
    background-color: rgb(100, 0, 139);
    /* box-shadow: -30px 30px 20px  rgba(0,0,0,0.4); */
    border-radius: 80%;
    -webkit-box-shadow: 0 0 30px rgb(0, 0, 0);
    box-shadow: 0 0 30px rgb(0, 0, 0);

}

.iconz img{
    transition: 0.4s;
}

.iconz img:hover{
    width:90px;
}

.expert{
    font-family: 'Londrina Solid', cursive;
    text-shadow: 2px 2px 2px #000000;
    color:rgb(1, 168, 132);
    font-weight: 200;
    font-size: 30px;
    transition:0.4s;
    cursor:pointer;
    
}

.expert:hover{
    color:rgb(255, 255, 255)
}


.Annu{
    color:white;
}

.langchange{
    position: absolute;
    top: 20px;
    right: 100px;
    width:30px;
    
}

.iconsdiv{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.mario{
    width:50%;
    margin:5em;
    box-shadow: -30px 30px 20px rgba(0,0,0,0.4);
   
}

.footer{
    background-color:rgb(27, 31, 88);
    color:white;
    width: 100%;
   
}

.forrow{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:3em;
}


.sicons{
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-items: space-around;
}

.sicons img{
    margin:2em;
    box-shadow: -30px 30px 20px rgba(0,0,0,0.4);
}

.sicons p{
    text-shadow: 2px 2px 2px #000000;
}

.pwoli{
    -webkit-box-shadow: 0 0 30px rgb(0, 0, 0);
    box-shadow: 0 0 30px rgb(0, 0, 0);
    background-color: rgb(187, 8, 8);
    margin-top: 2em;
    
}

