@media only screen and (max-width:1000px){
    
    

    .giffy{
        width:150px;
    }

    .title{
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;    
    }

    
    

    .myname{
        font-size: 20px;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center; 
    }

    .forrow{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

   

@media only screen and (max-width:600px){
    .sicons{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom:2em;
    }
}
    .iconz{
        display:flex;
        flex-direction:column;
    }

    .expert{
        font-size: 30px;
    }

    .mygif{
        width:100px;
    }

    .forrow{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .langchange{
        display: none;
    }

    .Mypic{
        width:130px;
    }

    .myname h1{
        font-size:20px;
    }
   
    
}